import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NewPatientFormComponent } from './core/new-patient-form/new-patient-form.component';
import { PatientViewComponent } from './core/patient-view/patient-view.component';
import { DashboardComponent } from './core/dashboard/dashboard.component';
import { TeamControlPanelComponent } from './core/team-control-panel/team-control-panel.component';
import { NewMemberFormComponent } from './core/new-member-form/new-member-form.component';
import { NewAdminFormComponent } from './core/new-admin-form/new-admin-form.component';
import { EditPatientFormComponent } from './core/edit-patient-form/edit-patient-form.component';
import { LoginComponent } from './core/login/login.component';
import { OnboardingComponent } from './core/onboarding/onboarding.component';

const routes: Routes = [
  {path:"", component: OnboardingComponent},
  {path:"login", component: LoginComponent},
  {path:"patient-view", component: PatientViewComponent},
  {path:"new-patient-form", component: NewPatientFormComponent},
  {path:"edit-patient-form", component: EditPatientFormComponent},
  {path:"dashboard", component: DashboardComponent},
  {path:"team-control-panel", component: TeamControlPanelComponent},
  {path:"new-member-form", component: NewMemberFormComponent},
  {path:"new-admin-form", component: NewAdminFormComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
